* {
    box-sizing: border-box;
}

body,
footer {
    margin: 0;
}

/* Navigation Bar */

.navBar {
    height: 48px;
    width: 100%;
    margin: 0;
    background-color: rgba(153, 221, 255, 0.8);
    position: fixed;
    display: flex;
    align-items: center;
}

.name {
    font-size: 32px;
    padding: 0 1%; 
}

.logo {
    margin-right: auto;
    /* height: 36px; */
    font-size: 32px;
}

.navBtn {
    background: none;
    border: none;
    /* height: 48px; */
    font-size: 36px;
}

.navBtn img{
    height: 48px;   
}

.navList {
    margin: 0;
}

.navListItem {
    display: block;
    list-style: none;
    padding: 10px 18px;
}

.navListItem a {
    color: white;
    font-size: 20px;
    text-decoration: none;
}

.closeBtn {
    font-size: 24px;
}

/* Welcome Cover */
.cover {
    height: 240px;
    width: 100%;
    background-color: slategrey;
    background-image: url(/remote-assignments/static/media/tech_cover.bee36bb7.jpg);
    background-size: cover;
    display: table;
    padding-top: 36px;
}

.coverTitle {
    margin: 0;
    text-align: center;
    vertical-align: middle;
    color: white;
    display: table-cell;
}

/* Section Part */

.sectionContainer {
    height: auto;
}

.sectionMainTitle {
    text-align: center;
}

.flexRow {
    display: flex;
    justify-content: center;
}

.box {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: rgba(156, 208, 226, 0.3);
    margin: 2%;
    padding: 1% 3%;
    border: solid black 1px;
}


.actionBtn {
    width: 160px;
    font-size: 24px;
    margin: auto;
    text-align: center;
    vertical-align: middle;
    background-color: #9AD1F3;
    border: solid 1px black;
}

.hiddenRow {
    display: none;
}
/* 大於1200px */

@media (min-width: 1201px){

    .navBtn {
        display: none;
    }

    .navList {
        display: inline-block;
    }

    .navListItem {
        display: inline-block;
        list-style: none;
        padding: 10px 18px;
    }

    .navListItem:hover,
    .navListItem:active {
        background-color: rgba(0, 100, 150, 0.3);
    }

    .sectionContainer {
        max-width: 1200px;
        margin: auto;
    }

}

/*   800px-1200px   */

@media (min-width: 801px) and (max-width: 1200px) {
 
    .navBtn {
        display: none;
    }

    .navList {
        display: inline-block;
    }

    .navListItem {
        display: inline-block;
        list-style: none;
        padding: 10px 18px;
    }

    .navListItem:hover,
    .navListItem:active {
        background-color: rgba(0, 100, 150, 0.3);
    }

    .sectionContainer {
        max-width: 90%;
        margin: auto;
    }

}

/*  500px - 800px */

@media (min-width: 501px) and (max-width: 800px) {

    .navBar {
        display: table;
    }
    
    .navBtn {
        float: right;
    }
    
    .navList {
        /* display: none; */
        margin: 0;
        padding: 0;
        width: 50%;
        position: fixed;
        background-color: rgba(100, 100, 100, 0.9);        top: 0;
        right: 0;
        height: -webkit-fill-available;
    }
    
    .closeBtn {
        position: fixed;
        height: 24px;
        top: 12px;
        right: 12px;
    }

    .navListItem {
        padding: 24px 12px;
    }

    .navListItem:hover,
    .navListItem:active{
        background-color: rgba(0, 0, 0, 0.3);
    }
    
    .navListItem a {
        color: white;
        font-size: 20px;
        text-decoration: none;
    }

    .sectionContainer {
        max-width: 90%;
        margin: auto;
    }
}


/*    小於 500px   */

@media (max-width: 500px) {

    .navBar {
        display: table;
    }
     
    .navBtn {
        float: right;
    }
    
    .navList {
        /* display: none; */
        margin: 0;
        padding: 0;
        width: 50%;
        position: fixed;
        background-color: rgba(100, 100, 100, 0.9);
        top: 0;
        right: 0;
        z-index: 50;
        height: -webkit-fill-available;
    }
    
    .closeBtn {
        position: fixed;
        height: 24px;
        top: 12px;
        right: 12px;
    }

    .navListItem {
        padding: 24px 12px;
    }

    .navListItem:hover,
    .navListItem:active {
        background-color: rgba(0, 0, 0, 0.3);
    }

    .flexRow {
        flex-wrap: wrap;
    }

    .box {
        margin: 5%;
    }

}

